import { Component, NgZone, OnInit, ViewChild } from '@angular/core';
import { Router } from "@angular/router";
import { ApiservicesService } from '../../provider/apiservices.service';
import { IonSlides, } from '@ionic/angular';
import { URLS, APP_CONFIG } from '../../../../src/assets/constant';
import { DataTransferService } from '../../services/data-transfer.service';

@Component({
  selector: 'app-say-cheers-slide-detail',
  templateUrl: './say-cheers-slide-detail.page.html',
  styleUrls: ['./say-cheers-slide-detail.page.scss'],
})

export class SayCheersSlideDetailPage implements OnInit{

  @ViewChild('slideWithNav', { static: false }) slideWithNav: IonSlides; 
  sliderIndex:any;
  cheerBoardData:any;
  showData:boolean=false;
  cheersSlide:any;
  cheerBoard:any;
  value:number;
  myInfiniteScroll:any;
  myinfiniteScrollCall:boolean=false;
  @ViewChild(IonSlides) slides:IonSlides;
  // @ViewChild(Slides) slides:Slides;
  activeSliderIndexDetect:number = 0;
  // request_tab:any;

  constructor(private router:Router,private apiservices:ApiservicesService,private zone:NgZone,
    private sendData:DataTransferService) {
      
    this.cheerBoardData=this.sendData.sayCheers;
    // this.request_tab=this.sendData.myParam;
    console.log('clicked', this.cheerBoardData);
    this.showData=true;
    this.cheersSlide={
      speed:"500",initialSlide:this.cheerBoardData.index,slidesPerView:1,spaceBetween:10,pager:true,
    }

  }









  ngOnInit(){ }

  goToMessage(cheerData,scrollValue){
    this.zone.run(()=>{
      this.sendData.myParam=cheerData;
      this.sendData.autoScroll=scrollValue;
      this.router.navigate(["/say-cheers-detail"]);
    });
  }

  seeNextSlide(ind){
    this.zone.run(()=>{
      this.sliderIndex=ind;
      console.log('clicked', ind)
      this.slideWithNav.isEnd().then((val) => {
        if(val==true){
          // this.router.navigate(['/enternumber']);
        }else{
          this.slideWithNav.slideNext(500, true);
        }
      });
    });
  }

  seePreSlide(){
    this.zone.run(()=>{
      this.slideWithNav.getActiveIndex().then((num)=>{
        console.log('preview Slider==', num);
        if(num>=1){
          this.slideWithNav.lockSwipes(false).then(()=>{
            this.slideWithNav.slideTo(num-1, 500, true).then(()=>{
              console.log('goBack slide sucessfully');
            })
          });
        }
      });
    });
  }

  slideChangeDetect(){
    this.zone.run(()=>{
      console.log("index==", this.activeSliderIndexDetect);
      if (this.cheerBoardData.allData.length >= 10) {
        if (this.activeSliderIndexDetect == this.cheerBoardData?.allData.length - 5) {
          this.value = this.cheerBoardData?.allData.length;
          this.leaderCheerData();
        }
      }
    });
  }

  ionSlideDidChange(){
    this.zone.run(()=>{
      console.log("this is ionSlideDidChange2");
      setTimeout(() => {
        // this.activeSliderIndexDetect = this.slides.getActiveIndex();
        console.log("thought auto id 1==", this.activeSliderIndexDetect);
      }, 500);
      // this.CommentDetailsList();
      this.slideWithNav.isEnd().then(res=>{
        if(res==true){
          this.value = this.cheerBoardData.allData.length;
          this.leaderCheerData();
        }
      });
    });
  }

  leaderCheerData(){
    this.zone.run(()=>{
      console.log("noticeBoard List api call function");
      let apikey={
        // "request_tab":this.request_tab,
        "limit":this.value,
      }
      this.apiservices.apiCallWithLoginToken(URLS.leaderCheerApiUrl, apikey).subscribe((result)=>{
        console.log("noticeBoard List api result==", result);
        if(result.status==1 || result.status=='1'){
          this.cheerBoardData.allData= this.cheerBoardData.allData.concat(result.data.boardlist.board_list);
          console.log("this.cheerBoardData.allData==", this.cheerBoardData.allData);
        }else{
          console.log("noticeBoard List api error==", result);
        }
      },err =>{
        this.apiservices.showToastMessage(JSON.stringify(err),'top',3000,'redBg');
      });
    });
  }








  
}